import { Fragment, memo, useContext } from 'react';
import { css, Global } from '@emotion/react';

import { ThemeToggleContext } from '@/contexts/ThemeToggleContext.ts';
import { event } from '@/utils/gtags.ts';

function ThemeModeSwitch({ isWhite = false }: { isWhite?: boolean }) {
  const { themeToggler, theme } = useContext(ThemeToggleContext);

  const handleClickSwitch = () => {
    themeToggler();
    event('click_theme_toggle', { mode: theme === 'dark' ? 'light' : 'dark' });
  };
  return (
    <Fragment>
      <Global styles={BodyStyle} />
      <button
        onClick={handleClickSwitch}
        className={`theme-toggler ${isWhite ? 'is-white' : ''}`}
        aria-label="theme toggle button"
        type="button"
      >
        <div id="switch">
          <div className="mode"></div>
        </div>
      </button>
    </Fragment>
  );
}

export default memo(ThemeModeSwitch);

const BodyStyle = css`
  body {
    .theme-toggler {
      overflow: hidden;
      margin: 6px;
      padding: 2px;
      background: transparent;
      border-radius: 50%;
      transition: all 0.3s ease;

      #switch {
        position: relative;
        overflow: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 21px;
        height: 21px;
        padding: 0;
        background: transparent;
        border: 0;
      }

      .mode {
        position: relative;
        width: 18px;
        height: 18px;
        opacity: 0.8;
        background: #000;
        clip-path: path(
          'M17.5306 13.5882C16.6384 13.8558 15.689 14 14.7044 14C9.5004 14 5.28174 9.97061 5.28174 5.00005C5.28174 3.32422 5.76127 1.75538 6.59643 0.411865C2.77326 1.55853 0 4.96994 0 9.00001C0 13.9706 4.21866 18 9.42265 18C12.8721 18 15.8887 16.2296 17.5306 13.5882Z'
        );
        border-radius: 50%;
        transition: all 0.45s ease;

        &:before {
          /* sun */
          content: '';
          position: absolute;
          z-index: -1;
          top: 50%;
          left: 50%;
          transform: translateX(-50%) translateY(-50%);
          width: 5px;
          height: 5px;
          opacity: 0;
          background: inherit;
          border-radius: 50%;
          transition: box-shadow 0.4s 0s ease;
        }

        &:after {
          /* moon */
          content: '';
          position: absolute;
          top: -20%;
          left: 30%;
          width: 90%;
          height: 90%;
          background: rgb(255 255 255 / 1);
          border-radius: 50%;
          transition: all 0.3s ease;
        }

        &:hover {
          opacity: 1;
        }
      }

      &:hover {
        background: rgb(215 215 215 / 0.9);
      }

      &.is-white {
        .mode {
          background: #fff;
        }

        &:hover {
          background: rgb(215 215 215 / 0.2);
        }
      }
    }

    &.dark {
      .theme-toggler {
        background: transparent;

        &:hover {
          background: rgb(255 255 255 / 0.1);
        }

        .mode {
          transform: scale(0.5);
          overflow: initial;
          opacity: 0.8;
          background: white;
          clip-path: none;

          &:before {
            opacity: 1;
            box-shadow:
              0 -16px 0 0 white,
              0 16px 0 0 white,
              -16px 0 0 0 white,
              16px 0 0 0 white,
              12px 12px 0 0 white,
              12px -12px 0 0 white,
              -12px 12px 0 0 white,
              -12px -12px 0 0 white;
          }

          /* stylelint-disable no-descending-specificity */

          &:after {
            transform: translateX(50%) translateY(-50%);
            opacity: 0;
            transition: background 0.45s ease;
          }
        }

        &.is-white {
          .mode {
            &:before {
              box-shadow:
                0 -16px 0 0 white,
                0 16px 0 0 white,
                -16px 0 0 0 white,
                16px 0 0 0 white,
                12px 12px 0 0 white,
                12px -12px 0 0 white,
                -12px 12px 0 0 white,
                -12px -12px 0 0 white;
            }
          }
        }
      }
    }
  }
`;
